/* Needed to show the "Enter VR" icon */
a-scene {position: absolute; height: 100%; width: 100%}

/*body { width: 100%; height: 100%; margin: 0; padding: 0; }*/

.top-menu {
  position: absolute;
  z-index: 1;
  padding: 5px;
  text-align: center;
  margin: auto;
  width: 100%;
  background-color: black;
  opacity: .4;
  border-bottom: 1px solid white
}
.top-menu-item {
  color: white;
  text-decoration: none;
  font-family: sans-serif;
  font-size: 14px;
  padding: 10px;
  border-radius: 2px;
  border: 1px solid;
  cursor: pointer;
  display: inline-block;
  margin: 2px;
}
.top-menu-item:hover {
  color: white;
  background-color: grey;
}
.top-menu-item:active {
  color: white;
  background-color: lightgrey;
  -webkit-box-shadow: 0 0 10px 1px white;
          box-shadow: 0 0 10px 1px white;
}


/* Fade in */
.fade-in {
  -webkit-animation: fade-in 0.4s ease both;
  animation: fade-in 0.4s ease both
}
@-webkit-keyframes fade-in {
  from {opacity: 0} to {opacity: 1}
}
@keyframes fade-in {
  from {opacity: 0} to {opacity: 1}
}


/* Fade out */
.fade-out {
  -webkit-animation: fade-out 0.4s ease-out both;
  animation: fade-out 0.4s ease-out both;
}
@-webkit-keyframes fade-out {
  0% {opacity: 1} 100% {opacity: 0}
}
@keyframes fade-out {
  0% {opacity: 1} 100% {opacity: 0}
}


/* Fade in long */
.fade-in-long {
  -webkit-animation: fade-in-long 2s ease both;
  animation: fade 2s ease both
}
@-webkit-keyframes fade-in-long {
  from {opacity: 0} to {opacity: 1}
}
@keyframes fade-in-long {
  from {opacity: 0} to {opacity: 1}
}


